import React, { useState } from 'react';
import { Box, Typography, Stack, Card, CardMedia, CardContent, Link, Chip, Button, Dialog, IconButton } from '@mui/material';
import { motion, AnimatePresence, useAnimation } from 'framer-motion';
import { useInView } from 'react-intersection-observer';
import EmojiEventsIcon from '@mui/icons-material/EmojiEvents';
import OpenInNewIcon from '@mui/icons-material/OpenInNew';
import YouTubeIcon from '@mui/icons-material/YouTube';
import CloseIcon from '@mui/icons-material/Close';

const Achievements = () => {
  const [selectedImage, setSelectedImage] = useState<string | null>(null);
  const [ref, inView] = useInView({
    threshold: 0.1,
    triggerOnce: true,
  });

  const controls = useAnimation();

  React.useEffect(() => {
    if (inView) {
      controls.start('visible');
    }
  }, [controls, inView]);

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.1
      }
    }
  };

  const itemVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: {
      opacity: 1,
      y: 0,
      transition: {
        duration: 0.5
      }
    }
  };

  const achievements = [
    {
      title: "Speaker at PyData NYC 2023",
      description: "Selected as a speaker at PyData NYC conference to present on advanced IoT security implementations and machine learning applications in cybersecurity. The presentation covered threat detection techniques, secure device communication protocols, and real-time anomaly detection using Python-based frameworks.",
      tech: ["Python", "IoT Security", "Machine Learning", "Public Speaking"],
      images: [
        "/images/pydata.png",
        "/images/pydata2.png"
      ],
      conferenceLink: "https://pydata.org/nyc2023/presentations#365192",
      youtubeLink: "https://www.youtube.com/watch?v=ix8iTD5yQp0"
    }
  ];

  const handleOpenImage = (image: string) => {
    setSelectedImage(image);
  };

  const handleCloseImage = () => {
    setSelectedImage(null);
  };

  return (
    <motion.div
      ref={ref}
      initial="hidden"
      animate={controls}
      variants={containerVariants}
      transition={{ staggerChildren: 0.1 }}
    >
      <Box sx={{ py: 8, px: 4, maxWidth: '1200px', margin: '0 auto' }}>
        <motion.div variants={itemVariants}>
          <Typography variant="h4" sx={{ 
            color: '#ccd6f6', 
            mb: 6,
            display: 'flex',
            alignItems: 'center',
            gap: 2,
            '&::before': {
              content: '""',
              display: 'block',
              width: '50px',
              height: '2px',
              backgroundColor: '#64ffda',
              marginRight: '20px'
            }
          }}>
            <EmojiEventsIcon sx={{ color: '#64ffda' }} />
            <span style={{ color: '#64ffda' }}>05.</span> Achievements
          </Typography>
        </motion.div>

        {achievements.map((achievement, index) => (
          <motion.div
            key={index}
            variants={itemVariants}
          >
            <Card
              sx={{
                bgcolor: 'rgba(17, 34, 64, 0.7)',
                mb: 4,
                overflow: 'hidden',
                borderRadius: '10px',
                border: '1px solid rgba(100, 255, 218, 0.1)',
                '&:hover': {
                  transform: 'translateY(-8px)',
                  boxShadow: '0 20px 30px -15px rgba(2,12,27,0.7)',
                  '& .achievement-title': {
                    color: '#64ffda',
                  }
                },
                transition: 'all 0.4s ease',
              }}
            >
              <Box sx={{ 
                display: 'grid', 
                gridTemplateColumns: { xs: '1fr', md: 'repeat(2, 1fr)' }, 
                gap: 3, 
                p: 3
              }}>
                {achievement.images.map((image, i) => (
                  <motion.div
                    key={i}
                    whileHover={{ scale: 1.03 }}
                    transition={{ type: "spring", stiffness: 300 }}
                  >
                    <CardMedia
                      component="img"
                      height="300"
                      image={process.env.PUBLIC_URL + image}
                      alt={`PyData NYC Presentation ${i + 1}`}
                      sx={{
                        borderRadius: '6px',
                        objectFit: 'cover',
                        cursor: 'pointer',
                        border: '2px solid rgba(100, 255, 218, 0.2)',
                        boxShadow: '0 10px 30px -15px rgba(2,12,27,0.7)'
                      }}
                      onClick={() => handleOpenImage(process.env.PUBLIC_URL + image)}
                    />
                  </motion.div>
                ))}
              </Box>
              <CardContent sx={{ p: 4 }}>
                <Typography 
                  variant="h5" 
                  className="achievement-title"
                  sx={{ 
                    color: '#ccd6f6', 
                    mb: 2,
                    transition: 'color 0.3s ease'
                  }}
                >
                  {achievement.title}
                </Typography>
                <Typography sx={{ color: '#8892b0', mb: 3, lineHeight: 1.7 }}>
                  {achievement.description}
                </Typography>
                <Stack direction="row" spacing={1} flexWrap="wrap" sx={{ mb: 3 }}>
                  {achievement.tech.map((tech, i) => (
                    <motion.div
                      key={i}
                      whileHover={{ scale: 1.05, y: -3 }}
                      whileTap={{ scale: 0.95 }}
                    >
                      <Chip
                        label={tech}
                        sx={{
                          bgcolor: 'rgba(100, 255, 218, 0.1)',
                          color: '#64ffda',
                          fontSize: '0.85rem',
                          my: 0.5,
                          '&:hover': {
                            bgcolor: 'rgba(100, 255, 218, 0.2)',
                          }
                        }}
                      />
                    </motion.div>
                  ))}
                </Stack>
                <Stack direction={{ xs: 'column', sm: 'row' }} spacing={2} alignItems={{ xs: 'flex-start', sm: 'center' }}>
                  <motion.div
                    whileHover={{ scale: 1.05 }}
                    whileTap={{ scale: 0.95 }}
                  >
                    <Button
                      variant="outlined"
                      component={Link}
                      href={achievement.conferenceLink}
                      target="_blank"
                      rel="noopener noreferrer"
                      startIcon={<OpenInNewIcon />}
                      sx={{
                        color: '#64ffda',
                        borderColor: '#64ffda',
                        '&:hover': {
                          borderColor: '#64ffda',
                          backgroundColor: 'rgba(100, 255, 218, 0.1)'
                        }
                      }}
                    >
                      Conference Profile
                    </Button>
                  </motion.div>
                  <motion.div
                    whileHover={{ scale: 1.05 }}
                    whileTap={{ scale: 0.95 }}
                  >
                    <Button
                      variant="outlined"
                      component={Link}
                      href={achievement.youtubeLink}
                      target="_blank"
                      rel="noopener noreferrer"
                      startIcon={<YouTubeIcon />}
                      sx={{
                        color: '#ff0000',
                        borderColor: '#ff0000',
                        '&:hover': {
                          borderColor: '#ff0000',
                          backgroundColor: 'rgba(255, 0, 0, 0.1)'
                        }
                      }}
                    >
                      Watch Talk
                    </Button>
                  </motion.div>
                </Stack>
              </CardContent>
            </Card>
          </motion.div>
        ))}
      </Box>

      {/* Image Lightbox Dialog */}
      <Dialog
        open={!!selectedImage}
        onClose={handleCloseImage}
        maxWidth="lg"
        PaperProps={{
          style: {
            backgroundColor: 'transparent',
            boxShadow: 'none',
            overflow: 'hidden'
          }
        }}
      >
        <AnimatePresence>
          {selectedImage && (
            <motion.div
              initial={{ opacity: 0, scale: 0.8 }}
              animate={{ opacity: 1, scale: 1 }}
              exit={{ opacity: 0, scale: 0.8 }}
              transition={{ type: "spring", stiffness: 300 }}
              style={{ position: 'relative' }}
            >
              <img 
                src={selectedImage} 
                alt="Achievement" 
                style={{ 
                  maxWidth: '90vw', 
                  maxHeight: '90vh',
                  borderRadius: '8px',
                  border: '2px solid rgba(100, 255, 218, 0.3)',
                }}
              />
              <IconButton
                onClick={handleCloseImage}
                sx={{
                  position: 'absolute',
                  top: -20,
                  right: -20,
                  color: '#ffffff',
                  bgcolor: 'rgba(17, 34, 64, 0.9)',
                  '&:hover': {
                    bgcolor: 'rgba(100, 255, 218, 0.2)',
                  }
                }}
              >
                <CloseIcon />
              </IconButton>
            </motion.div>
          )}
        </AnimatePresence>
      </Dialog>
    </motion.div>
  );
};

export default Achievements;