import React, { useState, useEffect } from 'react';
import { Box, Typography, Stack, Link, Chip, Card, CardMedia, CardContent, CardActions, Button } from '@mui/material';
import { motion, AnimatePresence } from 'framer-motion';
import CodeIcon from '@mui/icons-material/Code';
import GitHubIcon from '@mui/icons-material/GitHub';
import OpenInNewIcon from '@mui/icons-material/OpenInNew';

// Import only the available image
import telehealth from '../images/telehealth.png';

const Projects = () => {
  const projects = [
    {
      title: "Cybersecurity Framework for Telehealth Services",
      description: "Designed and implemented a security architecture for a mid-sized healthcare provider's telehealth services, focusing on safeguarding sensitive health data. Developed a multi-layered defense strategy, incorporating network segmentation and secure protocols.",
      tech: ["Security Architecture", "Network Segmentation", "Healthcare Security", "HIPAA Compliance"],
      image: "/images/telehealth.png",
      imageImport: telehealth,
      github: "",
      live: ""
    },
    {
      title: "Heritage Asset Monitoring System",
      description: "Built a Linux-based IoT system to monitor temperature, humidity, and pressure, ensuring artifact preservation through real-time adjustments. Integrated cloud-based MQTT communication and a dashboard to trigger alerts and maintain optimal conditions.",
      tech: ["IoT", "MQTT", "Linux", "Real-time Monitoring", "Cloud Integration"],
      image: "/images/heritage.png",
      imageImport: null,
      github: "https://github.com/yourusername/heritage-monitoring",
      live: ""
    },
    {
      title: "Smart Plant Monitoring System",
      description: "Developed an IoT-based plant care system using the Silicon Labs Nano Matter Board as the hub, integrating Matter protocol and Thread network for seamless communication.",
      tech: ["Matter Protocol", "Thread Network", "IoT", "Silicon Labs"],
      image: "/images/plant.png",
      imageImport: null,
      github: "",
      live: "https://your-demo-link.com"
    }
  ];

  // Extract all unique skills from projects - fixed TypeScript issue with Set
  const allSkillsArray = projects.flatMap(project => project.tech);
  const allSkills = Array.from(new Set(allSkillsArray));
  
  // State for filtered skills
  const [selectedSkills, setSelectedSkills] = useState<string[]>([]);
  const [filteredProjects, setFilteredProjects] = useState(projects);

  // Filter projects based on selected skills
  useEffect(() => {
    if (selectedSkills.length === 0) {
      setFilteredProjects(projects);
    } else {
      setFilteredProjects(
        projects.filter(project => 
          project.tech.some(skill => selectedSkills.includes(skill))
        )
      );
    }
  }, [selectedSkills]);

  // Toggle skill selection
  const toggleSkill = (skill: string) => {
    if (selectedSkills.includes(skill)) {
      setSelectedSkills(selectedSkills.filter(s => s !== skill));
    } else {
      setSelectedSkills([...selectedSkills, skill]);
    }
  };

  // Clear all filters
  const clearFilters = () => {
    setSelectedSkills([]);
  };

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
    >
      <Box sx={{ py: 8, px: 4, maxWidth: '1200px', margin: '0 auto' }}>
        <Typography variant="h4" sx={{ 
          color: '#ccd6f6', 
          mb: 4,
          display: 'flex',
          alignItems: 'center',
          gap: 2,
          '&::before': {
            content: '""',
            display: 'block',
            width: '50px',
            height: '2px',
            backgroundColor: '#64ffda',
            marginRight: '20px'
          }
        }}>
          <CodeIcon sx={{ color: '#64ffda' }} />
          <span style={{ color: '#64ffda' }}>04.</span> Some Things I've Built
        </Typography>

        {/* Skills Filter Section */}
        <Box sx={{ mb: 4 }}>
          <Typography variant="h6" sx={{ color: '#8892b0', mb: 2 }}>
            Filter by skills:
          </Typography>
          <Stack direction="row" spacing={1} flexWrap="wrap" sx={{ mb: 2 }}>
            {allSkills.map((skill, index) => (
              <motion.div
                key={index}
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
              >
                <Chip
                  label={skill}
                  onClick={() => toggleSkill(skill)}
                  sx={{
                    bgcolor: selectedSkills.includes(skill) 
                      ? 'rgba(100, 255, 218, 0.3)' 
                      : 'rgba(100, 255, 218, 0.1)',
                    color: '#64ffda',
                    fontSize: '0.85rem',
                    my: 0.5,
                    cursor: 'pointer',
                    '&:hover': {
                      bgcolor: 'rgba(100, 255, 218, 0.2)',
                    }
                  }}
                />
              </motion.div>
            ))}
          </Stack>
          {selectedSkills.length > 0 && (
            <Button 
              variant="text" 
              onClick={clearFilters}
              sx={{ 
                color: '#64ffda', 
                borderColor: '#64ffda',
                fontSize: '0.85rem',
                '&:hover': {
                  borderColor: '#64ffda',
                  backgroundColor: 'rgba(100, 255, 218, 0.1)'
                }
              }}
            >
              Clear Filters
            </Button>
          )}
        </Box>

        <Box 
          sx={{ 
            display: 'grid',
            gridTemplateColumns: { xs: '1fr', md: 'repeat(2, 1fr)', lg: 'repeat(3, 1fr)' },
            gap: 4
          }}
        >
          <AnimatePresence>
            {filteredProjects.map((project, index) => (
              <motion.div
                key={project.title}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                exit={{ opacity: 0, scale: 0.9 }}
                transition={{ duration: 0.5, delay: index * 0.1 }}
                layout
                layoutId={project.title}
              >
                <Card
                  sx={{
                    height: '100%',
                    display: 'flex',
                    flexDirection: 'column',
                    bgcolor: 'rgba(17, 34, 64, 0.7)',
                    borderRadius: '4px',
                    '&:hover': {
                      transform: 'translateY(-5px)',
                      boxShadow: '0 10px 30px -15px rgba(2,12,27,0.7)'
                    },
                    transition: 'all 0.3s',
                  }}
                >
                  <CardMedia
                    component="img"
                    height="200"
                    image={project.imageImport || `${process.env.PUBLIC_URL}${project.image}`}
                    alt={project.title}
                    sx={{
                      objectFit: 'cover',
                      borderBottom: '1px solid rgba(100, 255, 218, 0.1)'
                    }}
                  />
                  <CardContent sx={{ flexGrow: 1 }}>
                    <Typography variant="h5" sx={{ color: '#ccd6f6', mb: 2 }}>
                      {project.title}
                    </Typography>
                    <Typography sx={{ color: '#8892b0', mb: 2 }}>
                      {project.description}
                    </Typography>
                    <Stack direction="row" spacing={1} flexWrap="wrap" sx={{ my: 2 }}>
                      {project.tech.map((tech, i) => (
                        <Chip
                          key={i}
                          label={tech}
                          sx={{
                            bgcolor: selectedSkills.includes(tech) 
                              ? 'rgba(100, 255, 218, 0.3)' 
                              : 'rgba(100, 255, 218, 0.1)',
                            color: '#64ffda',
                            fontSize: '0.85rem',
                            my: 0.5
                          }}
                        />
                      ))}
                    </Stack>
                  </CardContent>
                  <CardActions sx={{ p: 2, pt: 0 }}>
                    {project.github && (
                      <motion.div whileHover={{ scale: 1.2 }} whileTap={{ scale: 0.9 }}>
                        <Link 
                          href={project.github}
                          target="_blank"
                          rel="noopener noreferrer"
                          sx={{
                            color: '#ccd6f6',
                            '&:hover': { color: '#64ffda' }
                          }}
                        >
                          <GitHubIcon />
                        </Link>
                      </motion.div>
                    )}
                    {project.live && (
                      <motion.div whileHover={{ scale: 1.2 }} whileTap={{ scale: 0.9 }}>
                        <Link 
                          href={project.live}
                          target="_blank"
                          rel="noopener noreferrer"
                          sx={{
                            color: '#ccd6f6',
                            '&:hover': { color: '#64ffda' }
                          }}
                        >
                          <OpenInNewIcon />
                        </Link>
                      </motion.div>
                    )}
                  </CardActions>
                </Card>
              </motion.div>
            ))}
          </AnimatePresence>
        </Box>
      </Box>
    </motion.div>
  );
};

export default Projects;